<template>
  <v-container>
    <v-card>
      <v-card-title> <h2 class="mx-auto">Upload Images</h2> </v-card-title>
      <v-card-text>
        <v-file-input
          accept="image/*"
          multiple
          outlined
          dense
          @change="uploadFile"
          label="Upload Images"
        />
        <div class="mt-4" v-if="images.length > 0">
          <h2 class="text-center mb-3">
            Drag And Sort Images for serialization pages
          </h2>
          <v-row>
            <draggable
              class="draggable"
              handle=".movebtn"
              v-model="images"
              ghost-class="ghost"
              @end="onEnd"
            >
              <transition-group type="transition" name="flip-list">
                <v-col
                  cols="6"
                  md="4"
                  lg="3"
                  v-for="img in images"
                  :key="img.id"
                  class="sortableele"
                >
                  <div class="imagecol">
                    <img :src="img.imageUrl" class="sortable" />
                    <div class="controls">
                      <v-btn
                        fab
                        tile
                        color="error"
                        @click="deleteImage(img.id)"
                        small
                        class="deletebtn"
                      >
                        <v-icon>mdi-delete</v-icon>
                      </v-btn>
                      <v-btn
                        fab
                        tile
                        color="success"
                        @click="viewImage(img.imageUrl)"
                        small
                        class="deletebtn"
                      >
                        <v-icon>mdi-eye</v-icon>
                      </v-btn>
                      <v-btn
                        dark
                        fab
                        tile
                        color="pink"
                        small
                        class="deletebtn movebtn"
                      >
                        <v-icon>mdi-cursor-move</v-icon>
                      </v-btn>
                    </div>
                  </div>
                </v-col>
              </transition-group>
            </draggable>
          </v-row>
        </div>
      </v-card-text>
      <v-card-actions v-if="images.length > 0">
        <v-btn to="/generatepdf" class="success mx-auto" large tile
          >Generate PDF</v-btn
        >
      </v-card-actions>
    </v-card>
    <v-dialog width="600" v-model="dialog">
      <v-card>
        <v-card-text>
          <v-img :src="selected"></v-img>
        </v-card-text>
        <v-card-actions>
          <v-btn
            class="error mx-auto"
            large
            @click="dialog = !dialog"
            dark
            tile
          >
            close
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import draggable from "vuedraggable";
export default {
  components: {
    draggable,
  },
  data() {
    return {
      selected: "",
      dialog: false,
    };
  },
  methods: {
    viewImage(img) {
      this.dialog = !this.dialog;
      this.selected = img;
    },
    uploadFile(files) {
      files.forEach((file) => {
        let reader = new FileReader();
        reader.onloadend = () => {
          let obj = {
            imageUrl: reader.result,
            id: this.images.length,
          };
          this.$store.commit("addImage", obj);
        };
        reader.readAsDataURL(file);
      });
    },
    deleteImage(id) {
      this.$store.commit("deleteImage", id);
    },
    onEnd(e) {
      console.log(e.newIndex);
    },
  },
  computed: {
    images: {
      get() {
        return this.$store.state.images;
      },
      set(value) {
        this.$store.commit("updateList", value);
      },
    },
  },
};
</script>
<style lang="scss">
.flip-list-move {
  transition: transform 0.5s;
}
.imagecol {
  border: 2px solid black;
  display: inline-block;
  max-width: 250px;
  max-height: 250px;
}
.sortableele {
  display: inline-block;
  &-drag {
    opacity: 0;
  }
}
.sortable {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.deletebtn {
  align-self: center;
}
.controls {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
</style>