import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    images: [],
  },
  mutations: {
    updateList(state, values) {
      state.images = values;
    },
    addImage(state, value) {
      state.images.push(value);
    },
    deleteImage(state, id) {
      state.images = state.images.filter((i) => i.id != id);
    },
  },
  actions: {},
  modules: {},
});
